<template>
  <div class="m-products-list">
    <ul>
      <li
        v-for="item in nav"
        :key="item.key"
        :class="{ 's-nav-active': item.active }"
      >
        {{ item.name }}
      </li>
    </ul>
    <el-row>
      <my-item v-for="(item, index) in productList" :key="index" :meta="item" />
    </el-row>
  </div>
</template>
<script>
import myItem from './item.vue'
import api from '@/api/index.js'
export default {
  components: {
    myItem
  },
  data() {
    return {
      nav: [
        {
          key: "s-default",
          name: "",
          active: true,
        },
        {
          key: "s-price",
          name: "",
          active: false,
        },
        {
          key: "s-score",
          name: "",
          active: false,
        },
        {
          key: "s-comment",
          name: "",
          active: false,
        },
      ],
      productList: [],
    };
  },
  created () {
    api.getProductsList().then(res => {
      this.productList = res.data.data
    })
  }
};
</script>



